.form-bg{
    background:rgba(0, 0, 0, .65) url('../../Container/Images/FormBg.jpg') no-repeat center center/cover;
    background-blend-mode: darken;
}

.form-bg input{
    outline: none;
    border: none;
    background-color: rgba(255, 255, 255, 0.493);
    border-radius: 3px;
}

.form-bg select{
    outline: none;
    border: none;
    background-color: rgba(255, 255, 255, 0.493);
    border-radius: 3px;
}